* {
  border: 0;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

nav, footer, header, aside {
  display: block;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a, a:visited, a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
  max-width: 100%;
  height: auto;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}

.footer__info {
  color: #282828;
  text-align: center;
  padding: 20px 0;
  font-size: 16px;
  line-height: 25px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 30px 70px;
  display: flex;
}

@media (width <= 768px) {
  .header {
    padding: 18px 15px;
  }
}

.header__list {
  grid-auto-flow: column;
  gap: 40px;
  margin-left: auto;
  display: grid;
}

.header__link {
  color: #252525;
  font-size: 20px;
  font-weight: bold;
}

.page {
  color: #000;
  background-color: #fff;
  font-family: Raleway, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
}

.page main {
  text-align: center;
  padding: 0 70px;
  font-size: 18px;
}

.page, .page__body, .page__grid {
  height: 100%;
}

.page__body.lock {
  overflow-y: hidden;
}

.page__grid {
  grid-template-rows: min-content 1fr min-content;
  max-width: 1920px;
  margin: 0 auto;
  display: grid;
}

@media (width <= 768px) {
  .page__grid {
    overflow-x: hidden;
  }
}

.logo {
  grid-auto-flow: column;
  align-items: center;
  gap: 15px;
  display: grid;
}

@media (width <= 768px) {
  .logo {
    gap: 8px;
  }
}

.logo__text {
  text-transform: capitalize;
  color: #252525;
  font-size: 30px;
  font-weight: bold;
}

@media (width <= 768px) {
  .logo__text {
    font-size: 24px;
  }
}

.logo__image {
  -o-object-fit: cover;
  object-fit: cover;
  border-radius: 50%;
  width: 75px;
  height: 75px;
}

@media (width <= 768px) {
  .logo__image {
    width: 60px;
    height: 60px;
  }
}

.privacy {
  padding: 30px 70px;
}

@media (width <= 768px) {
  .privacy {
    padding: 20px 15px;
  }
}

.privacy__title {
  padding-bottom: 40px;
}

.privacy__list .privacy__list {
  margin-left: 10px;
}

.privacy__list {
  padding-left: 10px;
}

.privacy__text {
  color: #141414;
  font-size: 18px;
  line-height: 27px;
}

.privacy__text:not(:last-child) {
  padding-bottom: 25px;
}

.privacy__text a:focus {
  outline: 3px dotted #141414;
}

@media (width <= 422px) {
  .privacy__text {
    font-size: 15px;
  }
}

.title {
  font-size: 36px;
  font-weight: 700;
  line-height: 1.2;
}

.title--blue {
  color: var(--color-blue-medium);
}

.title--underline {
  padding-bottom: 20px;
  position: relative;
}

.title--underline:before {
  content: "";
  background-color: var(--color-gray-lighter);
  opacity: .7;
  border-radius: 10px;
  width: 104px;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media (width <= 768px) {
  .title {
    font-size: 32px;
  }
}
